* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* STRUCTURE */
#root {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

/* HEADER */
header {
  background-color: #346abc;
  width: calc(100% - 26px);
  padding: 13px;
}

#header-logo {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  width: 150px;
  aspect-ratio: 1;
}

hr {
  margin: 20px 0;
}

/* MAIN */
.crimson {
  color: crimson;
}

#topic-selector {
  border: solid 1px black;
  border-radius: 5px;
  background-color: lightgray;
  font-size: 14px;
  font-weight: 700;
  color: black;
}
#topic-selector:focus {
  outline: none;
  box-shadow: gray 2px 2px 2px;
}

main {
  padding: 10px;
}

.nothing-found {
  font-weight: 700;
  font-size: 20px;
  text-decoration: underline;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  text-align: center;
}

#select-gui {
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 250px;
}
#select-gui p {
  text-align: center;
  margin-bottom: 10px;
}
#topic-selector {
  width: 200px;
}

#display-resources {
  max-width: 500px;
  min-width: 270px;
  margin: 10px auto;
}
#display-resources h1 {
  text-align: center;
  margin: 0 0 20px;
}
#display-resources table {
  border-collapse: collapse;
  width: 100%;
}
#display-resources table, #display-resources table thead th, #display-resources table tbody td {
  border: 1px solid black;
}
#display-resources table thead th, #display-resources table tbody td {
  padding: 5px;
}

.topic-definition {
  margin: 10px;
}

.view-selution-btn {
  margin-top: 5px;
  color: #2f62ad;
  text-decoration: underline solid #2f62ad;
  font-weight: 700;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}
.view-selution-btn:hover {
  cursor: pointer;
}

#understanding-task {
  padding: 5px;
  border: 1px solid black;
  width: 100%;
}
.selution-div p {
  color: rgb(31, 87, 87);
  font-weight: 600;
}
.singular-task {
  margin-top: 20px;
}

/* FOOTER */
footer {
  margin-top: 50px;
  width: calc(100% - 20px);
  padding: 10px;
  background-color: #2f62ad;
}